import { SimpleField } from "api/collectionTypes";
import RichTextEditor from "components/RichTextEditor";
import { FieldComponentProps } from "./String";

const RichText: React.FC<FieldComponentProps<SimpleField>> = ({ value, onValue }) => {
  return (
    <RichTextEditor
      style={{ height: "30vw", minHeight: 400 }}
      value={value}
      onChange={(val) => onValue(val)}
    ></RichTextEditor>
  );
};

export default RichText;
