import { EnumField } from "api/collectionTypes";
import Select from "components/Select";
import { FieldComponentProps } from "./String";

const Enum: React.FC<FieldComponentProps<EnumField>> = ({
  value,
  onValue,
  groundColor,
  name,
  field,
}) => {
  return (
    <Select
      groundColor={groundColor}
      options={[
        { label: "Select", value: "" },
        ...field.enumOptions.map((opt) => ({ label: opt, value: opt })),
      ]}
      name={name}
      onChange={(e) => onValue(e.target.value)}
      value={value}
    ></Select>
  );
};

export default Enum;
