import StringBadge from "./Badges/String";
import NumberBadge from "./Badges/Number";
import MapBadge from "./Badges/Map";
import ArrayBadge from "./Badges/Array";
import BooleanBadge from "./Badges/Boolean";
import DateBadge from "./Badges/Date";
import MediaBadge from "./Badges/Media";
import RichTextBadge from "./Badges/RichText";
import JsonBadge from "./Badges/Json";
import EnumBadge from "./Badges/Enum";
import PasswordBadge from "./Badges/Password";
import RelationBadge from "./Badges/Relation";
import SubcollectionBadge from "./Badges/Subcollection";
import AnyBadge from "./Badges/Any";
import StringComponent from "./Components/String";
import AnyComponent from "./Components/Any";
import NumberComponent from "./Components/Number";
import BooleanComponent from "./Components/Boolean";
import DateComponent from "./Components/Date";
import MediaComponent from "./Components/Media";
import RichTextComponent from "./Components/RichText";
import EnumComponent from "./Components/Enum";
import PasswordComponent from "./Components/Password";
import RelationComponent from "./Components/Relation";
import SubcollectionComponent from "./Components/Subcollection";
import ArrayValidation from "./Validations/Array";
import MapValidation from "./Validations/Map";
import JsonValidation from "./Validations/Json";
import DefaultValidation from "./Validations/Default";
import HashedPasswordComponent from "./Components/HashedPassword";
import HashedPasswordValidation from "./Validations/HashedPassword";

export const HashedPasswordField = {
  type: "hashed-password",
  Component: HashedPasswordComponent,
  validate: HashedPasswordValidation,
};

const fields = [
  { type: "string", Badge: StringBadge, Component: StringComponent, validate: DefaultValidation },
  { type: "number", Badge: NumberBadge, Component: NumberComponent, validate: DefaultValidation },
  { type: "map", Badge: MapBadge, Component: AnyComponent, validate: MapValidation },
  { type: "array", Badge: ArrayBadge, Component: AnyComponent, validate: ArrayValidation },
  {
    type: "boolean",
    Badge: BooleanBadge,
    Component: BooleanComponent,
    validate: DefaultValidation,
  },
  { type: "date", Badge: DateBadge, Component: DateComponent, validate: DefaultValidation },
  { type: "media", Badge: MediaBadge, Component: MediaComponent, validate: DefaultValidation },
  {
    type: "rich-text",
    Badge: RichTextBadge,
    Component: RichTextComponent,
    validate: DefaultValidation,
  },
  { type: "json", Badge: JsonBadge, Component: AnyComponent, validate: JsonValidation },
  { type: "enum", Badge: EnumBadge, Component: EnumComponent, validate: DefaultValidation },
  {
    type: "password",
    Badge: PasswordBadge,
    Component: PasswordComponent,
    validate: DefaultValidation,
  },
  {
    type: "relation",
    Badge: RelationBadge,
    Component: RelationComponent,
    validate: DefaultValidation,
  },
  {
    type: "collection",
    Badge: SubcollectionBadge,
    Component: SubcollectionComponent,
    validate: DefaultValidation,
  },
  { type: "any", Badge: AnyBadge, Component: AnyComponent, validate: DefaultValidation },
];

export default fields;
