import { SimpleField } from "api/collectionTypes";
import Input from "components/Input";
import { FieldComponentProps } from "./String";

const Number: React.FC<FieldComponentProps<SimpleField>> = ({
  value,
  onValue,
  groundColor,
  name,
}) => {
  return (
    <Input
      groundColor={groundColor}
      className="h-28px"
      type="number"
      name={name}
      onChange={(e) => onValue(e.target.value)}
      value={value}
    ></Input>
  );
};

export default Number;
