import React from "react";
import Button from "components/Button";
import { useFormik } from "formik";
import Label from "components/Label";
import FieldTypes from "components/Fields";
import { HashedPasswordField } from "components/Fields";
import { useDispatch } from "react-redux";
import classNames from "classnames";
import { AnyField } from "api/collectionTypes";

export interface DocumentFormProps {
  onSubmit: (a: { [key: string]: any } | null) => void;
  fields: AnyField[];
  editableDocument?: { [key: string]: any };
  docId?: string;
  groundColor: "black" | "white";
  level: number;
}

const DocumentForm: React.FC<DocumentFormProps> = ({
  onSubmit,
  fields,
  editableDocument,
  level,
  docId,
  groundColor,
}) => {
  const allFieldTypes = [...FieldTypes, HashedPasswordField];

  const initialValues = {
    password: () => "",
    string: () => "",
    "rich-text": () => "",
    json: () => ({}),
    map: () => ({}),
    array: () => [],
    media: (x: AnyField) => (x.type === "media" && x.mediaSingle ? null : []),
    date: () => null,
    enum: () => null,
    number: () => null,
    boolean: () => false,
    relation: (x: AnyField) => (x.type === "relation" && x.relationOneToOne ? "" : []),
    collection: () => [],
    document: (x: AnyField) =>
      x.type === "document" &&
      x.documentFields.reduce((a, b) => ({ ...a, [b.id]: initialValues[b.type] }), {}),
    any: () => "",
    "hashed-password": () => "",
  };

  const dispatch = useDispatch();

  const initial = fields.reduce(
    (a, b) => ({
      ...a,
      [b.id]:
        editableDocument && editableDocument[b.id]
          ? editableDocument[b.id]
          : initialValues[b.type](b),
    }),
    {}
  );

  const { values, errors, setFieldValue, handleSubmit, submitCount, isSubmitting } = useFormik({
    validateOnChange: false,
    initialValues: initial as { [index: string]: any },
    enableReinitialize: true,
    onSubmit: async (vals) => {
      await dispatch({ type: "SET_DOCUMENT_CHANGED", payload: false });
      await onSubmit(vals);
    },
    validate: (vals) => {
      const errors: any = {};
      fields.forEach((x) => {
        let fieldType = allFieldTypes.find((f) => f.type === x.type);
        if (!fieldType) return errors;
        let error = fieldType.validate(vals[x.id]);
        if (error) {
          errors[x.id] = error;
        }
      });

      console.log("ERRORs", errors);

      return errors;
    },
  });

  const hasChanged = !editableDocument || JSON.stringify(initial) !== JSON.stringify(values);

  React.useEffect(() => {
    dispatch({ type: "SET_DOCUMENT_CHANGED", payload: hasChanged });
  }, [hasChanged, dispatch]);

  return (
    <div className="h-full flex flex-col w-full">
      <div className="flex justify-between flex-wrap mb-2">
        <div>
          <div
            className={classNames("font-medium text-27px leading-none capitalize mb-3 mr-4", {
              "text-white": groundColor === "black",
            })}
          >
            {docId ? `Edit Document` : `Add Document`}
          </div>
          {!docId ? null : <div className="mb-3 text-xs text-white">Doc id: {docId}</div>}
        </div>
        <Button
          type="submit"
          onClick={() => handleSubmit()}
          disabled={isSubmitting || !hasChanged}
          className={`h-34px ${
            hasChanged
              ? "bg-fireck-4 hover:bg-fireck-4-hover"
              : "bg-gray-300 text-gray-600 cursor-default"
          }  mb-2`}
        >
          {isSubmitting ? "Loading..." : editableDocument != null ? "Save" : "Publish"}
        </Button>
      </div>
      <div className="flex-grow h-0 overflow-y-auto overflow-x-hidden pt-4 -mx-7 -mb-7 px-7">
        <div className="flex flex-wrap -mx-3">
          {Object.keys(values).length
            ? fields
                .filter((x) => !x.isDefault)
                .map((x, i) => {
                  const Component = allFieldTypes.find((f) => f.type === x.type)?.Component;

                  return (
                    <div
                      key={`field-${i}`}
                      className={`${
                        ["rich-text", "collection"].includes(x.type) ? "w-full" : "sm:w-1/2 w-full"
                      } px-3 mb-12`}
                    >
                      <Label
                        groundColor={groundColor}
                        className="mb-2 font-medium"
                        error={submitCount > 0 ? errors[x.id]?.toString() : null}
                      >
                        {x.id}{" "}
                      </Label>
                      {!Component ? null : (
                        <Component
                          onValue={(val) => setFieldValue(x.id, val)}
                          groundColor={groundColor}
                          value={values[x.id]}
                          name={x.id}
                          level={level}
                          field={x as never}
                        ></Component>
                      )}
                    </div>
                  );
                })
            : null}
        </div>
      </div>
    </div>
  );
};

export default DocumentForm;
