import { CollectionType } from "api/collectionTypes";
import { useEffect, useState } from "react";
import { getDocument } from "api/collections";

interface RelationFieldValueProps {
  value: string;
  collectionType: CollectionType | undefined;
  labelField: string;
}

const RelationFieldValue: React.FC<RelationFieldValueProps> = (props) => {
  const [valueLabel, setValueLabel] = useState("");

  useEffect(() => {
    const { value, labelField, collectionType } = props;
    if (collectionType) {
      (async () => {
        try {
          let res = await getDocument(collectionType.id, value);
          if (!res.error) {
            setValueLabel(res[labelField]);
          }
        } catch (er) {
          console.log(er);
        }
      })();
    }
  }, [props, valueLabel]);

  return <>{valueLabel}</>;
};

export default RelationFieldValue;
