import { SimpleField } from "api/collectionTypes";
import Input from "components/Input";
import { useEffect, useState } from "react";
import { FieldComponentProps } from "./String";

const Password: React.FC<FieldComponentProps<SimpleField>> = ({
  value,
  onValue,
  groundColor,
  name,
}) => {
  const [plain, setPlain] = useState("");
  const createPassword = async () => {
    const res = await fetch(window.location.origin + "/auth/createPassword", {
      method: "POST",
      body: JSON.stringify(plain),
    }).then((x) => x.json());
    return res.error ? null : res;
  };

  useEffect(() => {
    setPlain(value);
  }, [value]);

  return (
    <div className="w-full relative flex">
      <div className="flex-grow">
        <Input
          disabled={value}
          onBlur={() => {}}
          groundColor={groundColor}
          className="h-28px"
          type="password"
          value={plain}
          name={name}
          onChange={(e) => setPlain(e.target.value)}
        ></Input>
      </div>

      <div className="ml-1 select-none font-medium">
        {value ? (
          <div
            onClick={() => onValue("")}
            className="h-28px leading-28px text-xs rounded bg-red-FF0000 hover:bg-red-FF0000-hover text-white cursor-pointer px-3"
          >
            Clear
          </div>
        ) : (
          <div
            onClick={async () => {
              let password = await createPassword();
              onValue(password);
            }}
            className="h-28px leading-28px text-xs rounded bg-fireck-4 hover:bg-fireck-4-hover cursor-pointer px-3"
          >
            Save
          </div>
        )}
      </div>
    </div>
  );
};

export default Password;
