import { MediaField } from "api/collectionTypes";
import MultipleMediaInput from "components/MultipleMediaInput";
import SingleMediaInput from "components/SingleMediaInput";
import { FieldComponentProps } from "./String";

const Media: React.FC<FieldComponentProps<MediaField>> = ({ value, onValue, field }) => {
  return field.mediaSingle ? (
    <SingleMediaInput
      selectedFile={value}
      setSelectedFile={(file: string) => onValue(file)}
    ></SingleMediaInput>
  ) : (
    <MultipleMediaInput
      selectedFiles={value}
      setSelectedFiles={(files: string[]) => onValue(files)}
    ></MultipleMediaInput>
  );
};

export default Media;
