import { CollectionField } from "api/collectionTypes";
import SubcollectionField from "components/SubcollectionField";
import { FieldComponentProps } from "./String";

const Collection: React.FC<FieldComponentProps<CollectionField>> = ({
  value,
  onValue,
  level,
  field,
}) => {
  return (
    <SubcollectionField
      level={level}
      value={value}
      onValue={(arr) => onValue(arr)}
      fields={field.collectionFields}
    ></SubcollectionField>
  );
};

export default Collection;
