import { AnyField, StringField } from "api/collectionTypes";
import Input from "components/Input";
import Textarea from "components/Textarea";

export interface FieldComponentProps<T> {
  value: any;
  onValue: (val: any) => void;
  groundColor: "white" | "black";
  name: string;
  field: T;
  level: number;
}

const String: React.FC<FieldComponentProps<StringField>> = ({
  field,
  groundColor,
  value,
  onValue,
  name,
}) => {
  return field.stringLong ? (
    <Textarea
      groundColor={groundColor}
      value={value}
      onChange={(e) => onValue(e.target.value)}
      name={name}
    ></Textarea>
  ) : (
    <Input
      groundColor={groundColor}
      className="h-28px"
      type="text"
      value={value}
      name={name}
      onChange={(e) => onValue(e.target.value)}
    ></Input>
  );
};

export default String;
