import { Validation } from "./Default";

const Validate: Validation = (val) => {
  try {
    let obj = JSON.parse(JSON.stringify(val));
    if (typeof obj.map !== "function") {
      throw new Error("object is not an array");
    }
  } catch (error) {
    return "Wrong format";
  }
  return null;
};

export default Validate;
